import React from 'react';

const Slide = props => {
  const { image, title, content } = props.data;

  const style = {
    backgroundImage: `linear-gradient(to right bottom, rgba(63, 81, 181, 0.9), rgba(0, 150, 136, 0.9)), url(${image}`
  };

  return (
    <div className={`Slide ${props.hidden ? 'hidden' : null}`} style={style}>
      <h2>{title}</h2>
      <p>{content}</p>
    </div>
  )
};

export default Slide;