import React, { useState, useEffect } from 'react';
import Slide from './Slide';

const Carousel = ({ data, TIMER_DURATION = 5000 }) => {
  const [step, setStep] = useState(0);
  const [isRunning, setIsRunning] = useState(true);
  const [progress, setProgress] = useState(0);
  const PROGRESS_INTERVAL = 1000;

  useEffect(() => {
    if (isRunning) {
      const progressInterval = setInterval(() => {
        setProgress(progress + PROGRESS_INTERVAL / (TIMER_DURATION - PROGRESS_INTERVAL) * 100);
      }, PROGRESS_INTERVAL);
      return () => clearInterval(progressInterval);
    }
  }, [isRunning, progress]);

  useEffect(() => {
    if (isRunning) {
      const timeout = setTimeout(() => {
        nextSlide();
      }, TIMER_DURATION);
      return () => {
        clearTimeout(timeout);
        setProgress(0);
      };
    }
  }, [step, isRunning]);

  const nextSlide = () => {
    let nextStep;
    if (step === data.length - 1) {
      nextStep = 0;
    } else {
      nextStep = (step % data.length) + 1;
    }
    setStep(nextStep);
  };

  const previousSlide = () => {
    let nextStep;
    if (step === 0) {
      nextStep = data.length - 1;
    } else {
      nextStep = (step % data.length) - 1;
    }
    setStep(nextStep);
  };

  return (
    <div
      className="Carousel"
      onMouseEnter={e => setIsRunning(false)}
      onMouseLeave={e => setIsRunning(true)}
    >
      <div className="Carousel__slides">
        {data.map((item, index) => {
          return (
            <Slide
              data={item}
              key={index}
              hidden={step !== index}
            />);
        })}
      </div>
      <div className="Controls">
        <span className="Controls--left" onClick={previousSlide}>{'<'}</span>
        <span className="Controls--right" onClick={nextSlide}>{'>'}</span>
      </div>
      <div className="Progress-bar" style={{ width: progress + '%' }}>&nbsp;</div>
    </div>
  )
}

export default Carousel
